.container {
  max-width: 600px;
  margin: 40px auto;
  font-family: Arial, sans-serif;
  text-align: center;
}

body {
  background-color: #8FE3FA;
}

.language-selector button {
  background: none;
  border: none;
  font-size: 1.8rem;
  cursor: pointer;
  margin: 0 5px;
}

.language-selector button:focus {
  outline: none;
}

.language-selector button:hover {
  transform: scale(1.2);
}

a {
  text-decoration: none;
  color: #0077cc;
}

a:hover {
  text-decoration: underline;
}

.no-bullets {
  list-style: none;
  padding-left: 0;
  line-height: 2;
  text-align: left;
  max-width: 700px;
  margin: 0 auto 30px;
}

.no-bullets li {
  margin-bottom: 15px;
  line-height: 1.6;
}

.no-bullets a {
  margin: 10px;
}

p a {
  margin: 10px;
}

.about-me {
  max-width: 700px;
  margin: 20px auto;
  text-align: center;
  font-size: 1.1rem;
  line-height: 1.6;
}

.skill-list {
  list-style: none;
  padding-left: 0;
  text-align: left;
  max-width: 600px;
  margin: 0 auto 20px;
  line-height: 1.8;
}

